body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

h2, p {
  margin: 0;
}

.card {
  box-sizing: border-box;
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 17px;
  display: flex;
}

.card__img {
  align-self: center;
  width: 160px;
  height: 134px;
}

.card__title {
  color: #060b35;
  padding: 40px 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__subtitle {
  color: #616070;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__btn {
  color: #fff;
  text-transform: uppercase;
  cursor: pointer;
  background: #00acdc;
  border: none;
  border-radius: 5px;
  width: 166px;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

.card__btn:hover {
  color: #00acdc;
  background: #fff;
  border: 1px solid #00acdc;
}

.grade {
  justify-content: space-between;
  padding: 16px 0 24px;
  display: flex;
}

.grade__text {
  color: #060b35;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.stars {
  display: flex;
}

.stars__star {
  background: url("star.822d561e.svg") center no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars--1 :nth-child(-n+1), .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.price {
  justify-content: space-between;
  padding-bottom: 16px;
  display: flex;
}

.price__title {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.price__subtitle {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

